/*---------Footer Bottom--------------*/

.gotop {
	position: fixed;
	bottom: 30px;
	right: 30px;
	width: auto;
	height: auto;
	z-index: 9999;
	opacity: 0;
	-webkit-transform: translateY(-60px);
	transform: translateY(-60px);
	-webkit-transition: all 0.2s linear;
	transition: all 0.2s linear;
}
.gotop a {
	background-color: #0186d5;
	display: inline-block;
	position: relative;
	color: #fff;
	text-decoration: none;
	line-height: 0;
	padding: 20px;
	box-shadow: 0 9px 10px -6px rgba(0,0,0,0.8) !important;
	overflow: hidden;
}
.gotop a i.ar {
	-webkit-transition: all 0.2s linear 0.2s;
	transition: all 0.2s linear 0.2s;
}
.gotop a:hover i.ar {
	-webkit-transform: translateY(-25px) rotateY(360deg);
	transform: translateY(-25px) rotateY(360deg);
	opacity: 0;
	-webkit-transition: all 0.3s linear 0.2s;
	transition: all 0.3s linear 0.2s;
}
.gotop a i.ar1, .gotop a i.ar2 {
	position: absolute;
	bottom: 0%;
	left: 50%;
	width: auto;
	height: auto;
	-webkit-transform: translate(-50%, -50%) rotateZ(360deg);
	transform: translate(-50%, -50%) rotateZ(360deg);
	opacity: 0;
	-webkit-transition: all 0.2s linear 0.2s;
	transition: all 0.2s linear 0.2s;
}
.gotop a i.ar2 {
	-webkit-transform: translate(-50%, -50%) rotateZ(0deg);
	transform: translate(-50%, -50%) rotateZ(0deg);
	-webkit-transition: all 0.2s linear 0s;
	transition: all 0.2s linear 0s;
}
.gotop a:hover i.ar1, .gotop a:hover i.ar2 {
	bottom: 27%;
	opacity: 1;
	-webkit-transform: translate(-50%, -50%) rotateZ(0deg);
	transform: translate(-50%, -50%) rotateZ(0deg);
	-webkit-transition: all 0.2s linear 0.2s;
	transition: all 0.2s linear 0.2s;
}
.gotop a:hover i.ar2 {
	bottom: 20%;
	-webkit-transform: translate(-50%, -50%) rotateZ(0deg);
	transform: translate(-50%, -50%) rotateZ(0deg);
	-webkit-transition: all 0.2s linear 0.4s;
	transition: all 0.2s linear 0.4s;
}
.top-class {
	display: inline-block;
	-webkit-transform: translateX(0px);
	transform: translateX(0px);
	opacity: 1;
	-webkit-transition: all 0.2s linear;
	transition: all 0.2s linear;
}
.top-class-op {
	display: inline-block;
	-webkit-transform: translateY(60px);
	transform: translateY(60px);
	opacity: 0;
	-webkit-transition: all 0.2s linear;
	transition: all 0.2s linear;
}
/* Footer end */

