    25%{-webkit-transform: translateX(3px);transform: translateX(3px);}
    50%{-webkit-transform: translateX(-3px);transform: translateX(-3px);}
    75%{-webkit-transform: translateX(3px);transform: translateX(3px);}
    99%{-webkit-transform: translateX(-3px);transform: translateX(-3px);}
    0%{ -webkit-transform: translateX(0px); transform: translateX(0px);}
}

@keyframes moveshake 
{
    0%{-webkit-transform: translateX(-3px);transform: translateX(-3px);}
    25%{-webkit-transform: translateX(3px);transform: translateX(3px);}
    50%{-webkit-transform: translateX(-3px);transform: translateX(-3px);}
