}

.notification-inner-box > div.notification-img
{
    flex-shrink: 0;
    width: 100px;
    padding-right: 20px;
}

.notification-inner-box > div.notification-img > div
{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 0 3px rgba(0,0,0,0.35), 0 0 10px rgba(0,0,0,0.15);
}

.notification-inner-box > div.notification-img > div img
{
    width: 100%;
    height: 100%;
    min-height: 100%;
    object-fit: contain;
    object-position: center;
}



@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .notification-inner-box > div.notification-img > div img
    {
        width: auto;
        max-width: none;
        min-height: 62px;
    }
}

.notification-inner-box > div.notification-cnt
{
    width: calc(100% - 200px);
}

.notification-inner-box > div.notification-cnt a
{
    color: inherit;
}


.notification-inner-box > div.notification-del
{
    flex-shrink: 0;
    width: 100px;
    text-align: center;
    align-self: center;
}

.notification-inner-box > div.notification-del a
{
    font-size: 20px;
    color: #fff;
    font-weight: 600;
    height: 40px;
    width: 40px;
    line-height: 40px;
    background-color: #f15922;
    box-shadow: 0 0 15px rgba(0,0,0,0.25);
    border-radius: 50%;
    display: inline-block;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

.notification-inner-box > div.notification-del a:hover
{
    color: #f15922;
    background-color: #fff;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}


.notification-inner-box > div.notification-cnt h3
{
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 5px;
}

.notification-inner-box > div.notification-cnt h3 small
{
    color: #beb5b5;
    font-size: 12px;
    font-weight: 400;
    margin-left: 8px;
}


.notification-inner-box > div.notification-cnt
{
    max-height: 141px;
/*    height: 141px;*/
    overflow-y: hidden;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

.notification-inner-box > div.notification-cnt.height-toggle
{
    height: auto;
    max-height: none;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

.notification-inner-box > div.notification-cnt p
{
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 0px;
    text-overflow: ellipsis;
    width: 100%;
    overflow: hidden;
    /*white-space: nowrap;*/
}

.notification-inner-box > div.notification-cnt p b
{
    font-weight: 600;
}


.notification-inner-box > div.notification-cnt h4
{
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 5px;
    color: #3c97d3;
}

.notification-inner-box > div.notification-cnt p.tn-para
{
    margin-bottom: 15px;
}

@media screen and (max-width: 767px)
{
    .notification-wrapper-head .btn
    {
        min-width: inherit;
        padding: 5px 14px 5px;
    }
    .notification-inner-box > div.notification-img
    {
        width: 80px;
        padding-right: 10px;
    }
    .notification-inner-box > div.notification-del
    {
        width: 50px;
    }
    .notification-inner-box > div.notification-cnt
    {
        width: calc(100% - 130px);
    }
    .notification-inner-box > div.notification-cnt h3
    {
        font-size: 14px;
    }
    .notification-inner-box > div.notification-cnt p, .notification-inner-box > div.notification-cnt h4
    {
        font-size: 12px;
    }
    .notification-inner-box > div.notification-cnt p.tn-para
    {
        margin-bottom: 10px;
    }
    .notification-inner-box > div.notification-del a
    {
        font-size: 14px;
        height: 30px;
        width: 30px;
        line-height: 30px;
    }
    .notification-heading
    {
        margin-bottom: 15px;
        margin-top: 30px;
    }
    .notification-heading h2
    {
        font-size: 16px;
    }
    .notification-wrapper-head h2
    {
        font-size: 22px;
    }
}

@media screen and (max-width: 500px)
{
    .notification-inner-box > div.notification-img
    {
        display: none;
    }
    .notification-inner-box > div.notification-cnt
    {
        width: calc(100% - 50px);
    }
