/*----------Switch Bar---------*/

.switch_bar_list a.switch_bar_ico { display: flex; align-items: center; justify-content: center; color: #000; min-height: 72px; width: 100%;  -webkit-transition: all 0.2s linear;  transition: all 0.2s linear; }
.switch_bar_list:hover a.switch_bar_ico, .switch_bar_list.active a.switch_bar_ico { color: #fff; -webkit-transition: all 0.2s linear; transition: all 0.2s linear; }
.switch_bar_list { background-color: #fff; -webkit-transition: all 0.2s linear; transition: all 0.2s linear;}
.switch_bar_list:hover, .switch_bar_list.active { background-color: #3c97d3; -webkit-transition: all 0.2s linear; transition: all 0.2s linear;}
.switch_bar_list a.switch_bar_ico b { font-weight: 600; font-size: 16px;line-height: 72px; }
.switch_bar_list a.switch_bar_ico { position: relative;}
.switch_bar_list.active a.switch_bar_ico:after { content: ''; position: absolute; bottom: -12px; left: 50%; -webkit-transform: translateX(-50%); transform: translateX(-50%);
  width: 0; 
  height: 0; 
  border-left: 18px solid transparent;
  border-right: 18px solid transparent;
  border-top: 12px solid #3c97d3;
}

.switch_bar_list  .sdm_ico{ background-position: center; background-size: contain; background-repeat: no-repeat; width: 41px; height: 34px; margin-right: 10px;  display: inline-block; }
.switch_bar_list  .sdmico_general_info{ background-image: url(data:image/png;base64,); }
.switch_bar_list.active > a > .sdmico_general_info, .switch_bar_list:hover > a > .sdmico_general_info{ background-image: url(data:image/png;base64,); }
.switch_bar_list  .sdmico_genpro{ background-image: url(data:image/png;base64,); }
.switch_bar_list.active > a > .sdmico_genpro, .switch_bar_list:hover > a > .sdmico_genpro{ background-image: url(data:image/png;base64,); }
.switch_bar_list .sdmico_genagent{ background-image: url(data:image/png;base64,); }
.switch_bar_list.active > a > .sdmico_genagent, .switch_bar_list:hover > a > .sdmico_genagent{ background-image: url(data:image/png;base64,); }
.switch_bar_list .sdmico_genrun{ background-image: url(data:image/png;base64,); }
.switch_bar_list.active > a > .sdmico_genrun, .switch_bar_list:hover > a > .sdmico_genrun{ background-image: url(data:image/png;base64,); }
.switch_bar_list .sdmico_genredeem{ background-image: url(data:image/png;base64,); }
